.bmi-calculator {
  max-width: 100%;
  margin: 0 auto;
  padding: 20px;
  scroll-margin-top: 60px;
  background-color: #111;
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(255, 69, 0, 0.3);
  font-family: 'Roboto', 'Arial', sans-serif;
  color: #fff;
}

.bmi-calculator h2 {
  color: #ff4500;
  font-size: 2rem;
  text-align: center;
  margin-bottom: 20px;
  text-transform: uppercase;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.calculator-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

form {
  width: 100%;
}

.input-group {
  margin-bottom: 15px;
}

label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: #ffd700;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.9rem;
}

input[type="number"],
select {
  width: 100%;
  padding: 12px;
  border-radius: 6px;
  border: 2px solid #ff4500;
  background-color: #222;
  color: #fff;
  font-size: 1rem;
  box-shadow: 0 4px 6px rgba(255, 69, 0, 0.2);
  transition: all 0.3s ease;
  -webkit-appearance: none;
  appearance: none;
}

input[type="number"]:focus,
select:focus {
  border-color: #ffd700;
  box-shadow: 0 0 0 2px rgba(255, 215, 0, 0.5);
}

.radio-group {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.radio-group label {
  flex: 1 0 40%;
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: #222;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.radio-group label:hover {
  background-color: #333;
}

.radio-group input[type="radio"] {
  margin-right: 8px;
}

button {
  background-color: #ff4500;
  color: #fff;
  padding: 15px 25px;
  font-size: 1.1rem;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  width: 100%;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: all 0.3s ease;
}

button:hover, button:active {
  background-color: #ffd700;
  color: #111;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(255, 69, 0, 0.4);
}

.results {
  width: 100%;
  padding: 20px;
  border-radius: 10px;
  background-color: #222;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

.results h3 {
  color: #ffd700;
  font-size: 1.5rem;
  margin-bottom: 15px;
  text-transform: uppercase;
}

.results p {
  font-size: 1rem;
  margin-bottom: 10px;
}

.bmi-visualizer {
  width: 100%;
  height: 200px;
  margin-bottom: 20px;
  border-radius: 8px;
  overflow: hidden;
}

.chart-container {
  height: 200px;
  margin-bottom: 20px;
}

.cta-button {
  display: block;
  background-color: #ffd700;
  color: #111;
  padding: 15px 20px;
  font-size: 1rem;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  border-radius: 6px;
  transition: all 0.3s ease;
  text-align: center;
  margin-top: 20px;
}

.cta-button:hover, .cta-button:active {
  background-color: #ff4500;
  color: #fff;
  transform: translateY(-2px);
  box-shadow: 0 6px 10px rgba(255, 215, 0, 0.4);
}

@media (min-width: 768px) {
  .bmi-calculator {
    padding: 30px;
  }

  .bmi-calculator h2 {
    font-size: 2.5rem;
  }

  .calculator-container {
    flex-direction: row;
    gap: 30px;
  }

  form, .results {
    flex: 1;
  }

  .bmi-visualizer,
  .chart-container {
    height: 250px;
  }

  .cta-button {
    display: inline-block;
    padding: 15px 30px;
    font-size: 1.1rem;
  }
}