.footer {
  background-color: var(--primary-color); 
  color: var(--white);
  padding: 3rem 0; /* Aumenta il padding */
  margin-top: 2rem; /* Aggiungi margine superiore */
}

.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.social-links a {
  color: var(--white);
  text-decoration: none;
  font-weight: 500; /* Leggermente più audace */
}

.social-links a:hover {
  text-decoration: underline;
}

.social-links .fa-instagram {
  margin-right: 0.5rem;
  font-size: 1.2rem; /* Regola la dimensione dell'icona */
}

@media (max-width: 768px) {
  .footer-content {
    flex-direction: column;
    text-align: center;
  }

  .social-links {
    margin-top: 1rem;
  }
}