.services {
  padding: 50px 0;
  background-color: var(--background-color); /* Mantieni lo sfondo dinamico */
}

.services h2 {
  color: var(--primary-color); /* Mantieni il colore dinamico, dovrebbe essere arancione */
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 30px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.mySwiper {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 300px;
  height: 300px; /* Altezza aumentata per dare più spazio al contenuto */
}

.service-card {
  background-color: rgba(255, 255, 255, 0.1); /* Semi-transparent white background */
  border-radius: 10px;
  padding: 20px;
  box-shadow: inset 0 0 10px rgba(0,0,0,0.2); /* Inner shadow */
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center; 
  justify-content: flex-start; 
  overflow-y: auto; 
  color: #333; 
  font-size: 1.2rem;  
}

.service-card h3 {
  color: var(--accent-color); /* Mantieni il colore dinamico per i titoli */
  font-size: 1.5rem;
  margin-bottom: 10px;
  text-align: center; 
}

.service-card p {
  color: var(--text-color); /* Mantieni il colore dinamico per il testo */
  font-size: 1rem;
  text-align: center; 
}

.swiper-pagination-bullet {
  background-color: var(--text-color);
}

.swiper-pagination-bullet-active {
  background-color: var(--primary-color);
}

.swiper-button-next,
.swiper-button-prev {
  color: var(--primary-color); /* Mantieni il colore dinamico per le frecce */
  font-size: 2rem; 
}

.swiper-button-prev:hover,
.swiper-button-next:hover {
  color: orange; 
  transform: scale(1.1);
}