html {
  scroll-behavior: smooth;
}

body {
  scroll-padding-top: 60px;
}

.navigation {
  background-color: rgba(26, 26, 26, 0.9);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  padding: 0.5rem 1rem;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
}

.nav-logo {
  color: #ff4500;
  font-size: 1.5em;
  font-weight: bold;
  text-transform: uppercase;
}

.nav-links {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.nav-links li {
  margin-left: 1rem;
}

.nav-links a {
  color: var(--text-color);
  text-decoration: none;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 0.9em;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #ff4500;
}

.menu-toggle {
  display: none;
  background: none;
  border: none;
  color: white;
  font-size: 1.5em;
  cursor: pointer;
}

@media (max-width: 920px) {
  .menu-toggle {
    display: block;
  }

  .nav-links {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background-color: rgba(26, 26, 26, 0.95);
    flex-direction: column;
    align-items: center;
    padding: 1rem 0;
  }

  .nav-links.open {
    display: flex;
  }

  .nav-links li {
    margin: 0.5rem 0;
  }
}

@media (min-width: 921px) {
  .nav-links {
    display: flex !important;
  }
}