.testimonials {
  background-color: var(--background-color);
  padding: 50px 0;
}

.testimonials h2 {
  color: var(--primary-color);
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 30px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.testimonial-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  padding: 20px;
}

.testimonial-card {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.testimonial-placeholder {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: var(--primary-color);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  color: var(--text-color);
  margin-bottom: 15px;
}

.testimonial-text {
  color: var(--text-color);
  font-style: italic;
  margin-bottom: 10px;
}

.testimonial-name {
  color: var(--accent-color);
  font-weight: bold;
}