:root {
  --primary-color: #ff4500;  /* Arancione vivace */
  --secondary-color: #1e90ff;  /* Blu elettrico */
  --accent-color: #ffd700;  /* Oro */
  --background-color: #1a1a1a;  /* Grigio scuro quasi nero */
  --text-color: #ffffff;  /* Bianco */
  --highlight-color: #32cd32;  /* Verde lime */
}
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@700&family=Oswald:wght@500&display=swap');

body {
  font-family: 'Roboto Condensed', sans-serif;
  background-color: var(--background-color);
  color: var(--text-color);
  line-height: 1.6;
}

h1, h2, h3 {
  font-family: 'Oswald', sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (max-width: 768px) {
  .container {
    padding: 0 10px;
  }
}
.section-title {
  font-size: 2.5rem;
  color: var(--primary-color);
  text-align: center;
  margin-bottom: 2rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.btn {
  background-color: var(--primary-color);
  color: var(--text-color);
  padding: 12px 24px;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: bold;
  transition: all 0.3s ease;
  cursor: pointer;
}

.btn:hover {
  background-color: var(--accent-color);
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(255, 69, 0, 0.4);
}

@media (max-width: 768px) {
  .section-title {
    font-size: 2rem;
  }
}
@media (max-width: 768px) {
  .home h1 {
    font-size: 2rem;
  }

  .home p {
    font-size: 1rem;
  }

  .cta-button {
    padding: 0.8rem 1.5rem;
  }
}


section {
  padding-top: 60px; /* Aggiusta questo valore in base all'altezza del tuo header */
  margin-top: -60px;
}


html {
  scroll-behavior: smooth;
}

#services, #testimonials {
  scroll-margin-top: 60px; /* Same as scroll-padding-top */
}