.about {
  position: relative;
  padding: 50px 0;
  overflow: hidden;
}

.about-content {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}

.about-text-container {
  background-color: rgba(255, 255, 255, 0.9);
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  max-width: 600px;
  margin: 20px auto;
  transform: perspective(1000px) rotateX(5deg) translateY(50px);
  opacity: 0;
  transition: transform 1s ease, opacity 1s ease; /* Rallentato a 1s */
}

.about-text-container.visible {
  opacity: 1;
  transform: perspective(1000px) rotateX(5deg) translateY(0);
}

.about-text-container:hover {
  transform: perspective(1000px) rotateX(0deg) translateY(0);
}

.about h2 {
  font-size: 2.5rem;
  color: #14213d;
  margin-bottom: 20px;
  text-align: center;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

.about p {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #333;
  margin-bottom: 15px;
}

@media (max-width: 768px) {
  .about-text-container {
    padding: 20px;
    margin: 10px;
    transform: translateY(50px); /* Rimosso l'effetto 3D per mobile */
    transition: transform 0.8s ease, opacity 0.8s ease;
  }

  .about-text-container.visible {
    transform: translateY(0);
  }

  .about-text-container:hover {
    transform: translateY(0);
  }

  .about h2 {
    font-size: 2rem;
  }

  .about p {
    font-size: 1rem;
  }
}